import React from "react";
import "./App.scss";
import Main from "./containers/Main";

function App() {
  var lang = localStorage.getItem('lang');
  if (!lang)
  {
    lang = 'hu';
    localStorage.setItem('lang', lang);
  }
  return (
    <div>
      <Main />
    </div>
  );
}

export default App;
