import React, {useContext} from "react";
import Headroom from "react-headroom";
import "./Header.scss";
import ToggleSwitch from "../ToggleSwitch/ToggleSwitch";
import StyleContext from "../../contexts/StyleContext";
import {
  greeting_en,
  greeting_hu,
  workExperiences_en,
  workExperiences_hu,
  skillsSection_en,
  skillsSection_hu,
  contactInfo_hu,
  contactInfo_en,
  openSource,
  blogSection,
  talkSection_en,
  talkSection_hu,
  achievementSection_en,
  achievementSection_hu
} from "../../portfolio";

function Header() {
  var lang = localStorage.getItem("lang");
  var workExperiences = lang === "hu" ? workExperiences_hu : workExperiences_en;
  var talkSection = lang === "hu" ? talkSection_hu : talkSection_en;
  var achievementSection =
    lang === "hu" ? achievementSection_hu : achievementSection_en;
  var greeting = lang === "hu" ? greeting_hu : greeting_en;
  var skillsSection = lang === "hu" ? skillsSection_hu : skillsSection_en;
  var contactInfo = lang === "hu" ? contactInfo_hu : contactInfo_en;
  const {isDark} = useContext(StyleContext);
  const viewExperience = workExperiences.display;
  const viewOpenSource = openSource.display;
  const viewSkills = skillsSection.display;
  const viewAchievement = achievementSection.display;
  const viewBlog = blogSection.display;
  const viewTalks = talkSection.display;
  function changeLangWihReload() {
    var lang = localStorage.getItem("lang");
    var newLang = lang === "hu" ? "en" : "hu";
    localStorage.setItem("lang", newLang);
    window.location.reload();
  }

  return (
    <Headroom>
      <header className={isDark ? "dark-menu header" : "header"}>
        <a href="/" className="logo">
          <span className="logo-name">{greeting.username}</span>
        </a>
        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label
          className="menu-icon"
          htmlFor="menu-btn"
          style={{color: "white"}}
        >
          <span className={isDark ? "navicon navicon-dark" : "navicon"}></span>
        </label>
        <ul className={isDark ? "dark-menu menu" : "menu"}>
          {
            <li className="lang-changer">
              {lang === "hu" ? (
                <img
                  onClick={() => changeLangWihReload()}
                  src={require("../../assets/images/en.jpg")}
                  alt="british flag"
                  className="card-image"
                ></img>
              ) : (
                <img
                  onClick={() => changeLangWihReload()}
                  src={require("../../assets/images/hun.jpg")}
                  alt="hungarian flag"
                  className="card-image"
                ></img>
              )}
            </li>
          }
          {viewSkills && (
            <li>
              <a href="#skills">{skillsSection.title}</a>
            </li>
          )}
          {viewExperience && (
            <li>
              <a href="#experience">{workExperiences.title}</a>
            </li>
          )}
          {viewOpenSource && (
            <li>
              <a href="#opensource">Open Source</a>
            </li>
          )}
          {viewAchievement && (
            <li>
              <a href="#achievements">{achievementSection.title}</a>
            </li>
          )}
          {viewBlog && (
            <li>
              <a href="#blogs">Blogs</a>
            </li>
          )}
          {viewTalks && (
            <li>
              <a href="#talks">{talkSection.title}</a>
            </li>
          )}
          <li>
            <a href="#contact">{contactInfo.title}</a>
          </li>
          <li>
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <a>
              <ToggleSwitch />
            </a>
          </li>
        </ul>
      </header>
    </Headroom>
  );
}
export default Header;
